.TellieDropzone {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  height: 100%;
  margin-bottom: 8px;
  font-size: 16px;
  padding: 12px;
  overflow: hidden;

  &:hover {
    .TellieDropzone_preview {
      opacity: 0.5;
    }

    .TellieDropzone_drag-indicator {
      opacity: 1;
    }
  }

  &.lightMode {
    color: #1F1F1F;
  }

  .TellieDropzone_drag-indicator {
    opacity: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }
  
  .TellieDropzone_input {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
  }

  .TellieDropzone_preview {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    background-position: center;
    opacity: 1;
  }

  .TellieDropzone_preview-active {
    @extend .TellieDropzone_preview;
    opacity: 0.5;
  }

  .TellieDropzone_drag-indicator {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    .TellieDropzone_drag-indicator-inner {
      width: 100%;
      height: 100%;  
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      border-radius: 4px;
      cursor: pointer;

      .TellieDropzone
      icon  {
        margin: 0
      }
    
      .TellieDropzone_text {
        font-size: 11px;
        width: 104px;
        margin-top: 6px;
        text-align: center;
      }
    }
  }

  .TellieDropzone_drag-indicator-active {
    @extend .TellieDropzone_drag-indicator;
    opacity: 1;
  }
}