.IconButton {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
  border: none;
  background: none;
  outline: none;
  cursor: pointer;

  color: inherit;

  position: absolute;
  top: 0;

  height: 100%;

  &:hover {
    opacity: .35;
  }
}

.left {
  left: 4px;
}

.right {
  right: 4px;
}

.invertOpacity {
  opacity: .35;

  &:hover {
    opacity: 1;
  }
}